.mapList{
    border: 1px solid rgb(182 191 191);
    margin-top: 10px;
    border-radius: 10px;
    padding-left:10px;
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    justify-content: space-between;
    font-weight: normal;
    color: rgb(75, 75, 75);
    vertical-align: middle;
    
}

.mapList:hover{
    background-color: rgb(215, 241, 253);
}

.mapList h3{
    padding: 10px;
}
button{
    margin: 5px;
    border-radius: 8px;
    border: 1px solid rgb(182 191 191);
    padding: 3px;
    text-align: center;
    vertical-align: center;
}

button:hover{
cursor: pointer;
}